#root {
  width: 100vw;
  height: 100vh;
  background-color: whitesmoke;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 55vw;
  margin: 0;
  padding: 0;
  margin: auto;
}

img {
  width: 20px;
  height: 20px;
  border-radius: 10px;
}

.button {
  background-color: transparent;
  border: none;
}

.done {
  text-decoration: line-through;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 30px;
  width: 50vw;
  padding: 15px;
  margin: auto;
  margin-top: 15px;
}

.card {
  box-shadow: 0 2px 7px 1px rgba(0, 0, 0, 0.2);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.addButton {
  margin: 0;
  padding: 0;
  width: 50px;
  font-size: 50px;
  border: none;
  background-color: lightblue;
  color: white;
  border-radius: 30px 15px;
}

.formModal {
  display: flex;
  justify-content: space-between;
  width: 40vw;
}

.btnModal {
  border-radius: 30px;
  padding: 10px;
  border: none;
  background-color: lightblue;
  color: black;
  font-weight: bolder;
}

.inputModal {
  border: none;
  width: 90%;
}

.modal {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cardModal {
  padding: 20px;
  background-color: white;
}

.hide {
  display: none;
}

@media screen and (max-width: 800px) {

  .container {
    max-width: 90vw;
    margin: auto;
  }

  .item {
    width: 90%;
  }

  .formModal {
    width: 80vw;
  }
}

@media screen and (max-width: 1000px) {

  .container {
    max-width: 90vw;
    margin: auto;
  }

  .item {
    width: 90%;
  }
}